import mongoose from 'mongoose';
mongoose.Promise = global.Promise;

const Schema = mongoose.Schema;

const DepositSchema = mongoose.Schema({
	superAgent: {
		type: String,
		required: true
	},
	agent: {
		type: String,
		required: true
	},
	completedAt: {
		type: Date,
		required: true
	},
	orderResultAmount: {
		type: Number,
		required: true
	},
	orderNumber: {
		type: String,
		required: true
	},
	orderStatus: {
		type: String,
		default: 'Deposited'
	}
})

const Deposit = mongoose.model('deposit', DepositSchema);

export default Deposit;